@import url("https://www.w3schools.com/w3css/4/w3.css");
.nav-bar {
  z-index: 100;
  width: 100vw;
  margin: auto;
  padding: 1px;
  color: white;
  background: rgb(163, 200, 226);
  background: linear-gradient(0deg, rgba(29, 125, 170, 0.411) 0%, rgba(20, 22, 139, 0.171) 100%);
  position: fixed;
  backdrop-filter: blur(8px);
}
.nav-bar nav {
  justify-self: end;
  position: relative;
  top: 0;
  left: 0;
  left: 50%;
  width: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 0.65rem 0;
  backdrop-filter: blur(0px);
}
.nav-bar nav a {
  text-decoration: none;
}
.nav-bar nav a small {
  color: rgba(236, 236, 236, 0.753);
}

.menu-button {
  display: none;
}

@media screen and (max-width: 725px) {
  .menu-button {
    display: block;
    position: fixed;
    z-index: 300;
    color: white;
    font-size: 32px;
    top: 32px;
    left: 15px;
    padding: 6px;
    background-color: var(--blue-shade);
    border-radius: 6px;
  }
  .nav-bar {
    transition: all 0.3s;
    transform: translate(0, -20px);
  }
  .nav-bar.closed {
    width: 0;
    transform: translate(-150vw, -20px);
  }
  .nav-bar nav {
    padding: 7rem 0 0 0;
    position: fixed;
    left: 0;
    height: 102vh;
    width: 250px;
    flex-direction: column;
    align-items: unset;
    justify-content: unset;
    background: linear-gradient(0deg, rgba(29, 125, 170, 0.432) 0%, rgba(20, 22, 139, 0.336) 100%);
  }
  .nav-bar nav * {
    background-color: transparent;
    color: white;
    text-align: left;
    margin: 10px 0;
    padding: 16px 10px;
    align-self: left;
    border-bottom: 1px solid white;
  }
}